#quote-box {
	width: 550px;
	min-height: 290px;
	height: auto;
	background-color: white;
	border-radius: 2px;
	display: grid;
	grid-template-columns: 40px auto auto 40px;
	grid-template-rows: 40px auto auto auto 40px;
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

#text {
	font-size: 24px;
	grid-area: 2 / 2 / span 1 / span 2;
	margin: 0;
}

#text:before {
  font-family: "FontAwesome";
  font-weight: 300;
  content: "\f10d";
  display: inline-block;
  padding-right: 20px;
}

#author {
	font-size: 18px;
	grid-area: 3 / 2 / span 1 / span 2;
	margin: 0;
	text-align: right;
}

#author:before {
    /* "— ";  em dash */
	content: "\2014"; 
	padding-right: 7px;
}

#tweet-quote {
	text-decoration: none;
	font-size: 40px;
	grid-area: 4 / 2 / span 1 / span 1;
	align-self: end;
	justify-self: start;
	overflow: hidden;
	margin-top: 20px;
	margin-bottom: -6px;
}

#new-quote {
  height: 36px;
	font-size: 14px;
	color: white;
	padding: 0 10px;
	border: 0 solid transparent;
	border-radius: 3px;
	grid-area: 4 / 3 / span 1 / span 1;
	align-self: end;
	justify-self: end;
}

#new-quote:focus {
	outline: none;
}